.membership {
  padding-top: 117px;
  padding-bottom: 100px;
  background-color: $grey-blue;
  overflow: hidden;

  @media (max-width: $tablet-width-max) {
    padding-top: 79px;
  }

  @media (max-width: $mobile-width-max) {
    padding-top: 19px;
    padding-bottom: 70px;
  }
}

.membership ul {
  @include reset-list;
}

.membership__wrap {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
}

.membership h2 {
  @include reset-indents;
  padding-bottom: 37px;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-weight: 700;
  font-size: 49px;
  line-height: 49px;
  letter-spacing: 0.03em;
  color: $twilight-blue;

  @media (max-width: $tablet-width-max) {
    padding-bottom: 32px;
    font-size: 42px;
    line-height: 42px;
  }

  @media (max-width: $mobile-width-max) {
    padding-bottom: 25px;
    font-size: 32px;
    line-height: 32px;
  }
}

.membership__months-wrap {
  display: inline-flex;
  margin-bottom: 38px;
  padding-bottom: 12px;
  border-bottom: 2px solid rgba(120, 120, 120, 0.2);

  @media (max-width: $tablet-width-max) {
    margin-bottom: 45px;
  }

  @media (max-width: $mobile-width-max) {
    margin-bottom: 35px;
  }
}

.membership__months-list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 20px;
}

.membership__months-list li {
  &:not(:last-child) {
    padding-right: 46px;

    @media (max-width: $tablet-width-max) {
      padding-right: 54px;
    }

    @media (max-width: $mobile-width-max) {
      padding-right: 58px;
    }
  }
}

.membership__months-list a {
  position: relative;
  font-size: 18px;
  line-height: 27px;
  color: rgba($twilight-blue, 0.5);

  @media (max-width: $tablet-width-max) {
    font-size: 16px;
    line-height: 24px;
  }

  @media (max-width: $mobile-width-max) {
    font-size: 14px;
    line-height: 21px;
  }
}

.membership__months-item .membership__months-link--active {
  padding-bottom: 15px;
  color: $twilight-blue;
  border-bottom: 2px solid #d10a42;
}

.membership__item {
  &--active {
    background-image: $gradient-border;// для JS
  }

  &:hover,
  &:focus {
    background-image: $gradient-border;
  }

  &:hover .membership__btn,
  &:focus .membership__btn {
    color: $white;
    background-image: linear-gradient(90deg, #ed0233, #003eb7);
    background-origin: border-box;
    border-color: transparent;
  }
}


.membership__item p {
  @include reset-indents;

  &:first-of-type {
    padding-bottom: 13px;
    font-weight: 700;
    font-size: 24px;
    line-height: 27px;
    color: $twilight-blue;

    @media (max-width: $mobile-width-max) {
      padding-bottom: 11px;
      font-size: 22px;
      line-height: 25px;
    }
  }

  &:last-of-type {
    padding-bottom: 33px;
    font-weight: 700;
    font-size: 58px;
    line-height: 87px;
    color: $dark-blue;

    @media (max-width: $mobile-width-max) {
      padding-bottom: 30px;
      font-size: 46px;
      line-height: 69px;
    }
  }
}

.membership__price {
  z-index: 1;
}

.membership__shadow {
  position: absolute;
  top: 133px;
  font-weight: 700;
  font-size: 100px;
  line-height: 150px;
  color: #e8e8e8;
  opacity: 0.48;

  @media (max-width: $mobile-width-max) {
    top: 110px;
    font-size: 80px;
    line-height: 120px;
  }
}

.membership__item p:first-of-type + p {
  padding-bottom: 70px;
  color: $dark-grey-blue;

  @media (max-width: $mobile-width-max) {
    padding-bottom: 58px;
  }
}

.membership__list {
  display: flex;
  flex-wrap: wrap;

  @media (max-width: $desktop-width-max-2) {
    justify-content: center;
  }

  @media (max-width: $tablet-width-max) {
    flex-direction: column;
  }
}

.membership__list li {
  width: 360px;
  margin-bottom: 20px;
  padding: 7px;
  background-color: $white;

  &:not(:last-child):not(:nth-child(3n)) {
    margin-right: 40px;

    @media (max-width: $tablet-width-max) {
      margin-right: 0;
    }
  }

  @media (max-width: $desktop-width-max-2) {
    width: 310px;
  }

  @media (max-width: $tablet-width-max) {
    margin-bottom: 0;
  }

  @media (max-width: $tablet-width-max) {
    width: 442px;

    &:not(:last-child) {
      margin-bottom: 40px;
    }
  }

  @media (max-width: $mobile-width-max) {
    width: 290px;
  }
}

.membership__box {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  padding: 58px 47px;
  background-color: $white;

  @media (max-width: $desktop-width-max-2) {
    padding-right: 35px;
    padding-left: 35px;
  }

  @media (max-width: $mobile-width-max) {
    padding: 43px 34px;
  }
}

.membership__btn {
  padding: 24px 43px;
  color: $twilight-blue;
  border: 2px solid $twilight-blue;

  &:focus {
    color: $white;
    background-image: linear-gradient(90deg, #ed0233, #003eb7);
    background-origin: border-box;
    border-color: transparent;
  }

  // &--color {
  //   color: $white;
  //   background-image: linear-gradient(90deg, #ed0233, #003eb7);
  //   background-origin: border-box;
  //   border-color: transparent;
  // }
}
